<div class="container"><div class="d-flex flex-wrap w-100"><div class="form-group"><router-link class="btnSecondary w-100" to="/load">Загрузить данные</router-link></div><div class="form-group"><router-link class="btnSecondary ms-2 w-100" to="/parser">Парсинг сайта</router-link></div></div><div class="companySearch"><div class="form-group"><input class="form-control" type="search" v-model="search" @change="$_vision_index_searchCompany" placeholder="Поиск по названию или ID"></div></div><div class="contentTabs"><div class="contentTabMistake"><div class="companyResult"><div class="notifError">Всего компаний с несоответствиями найдено: {{lengthDouble.duble}}</div><div class="notifSuccess ms-3">Всего компаний: {{lengthDouble.orig}}</div></div><div class="companyListMain row g-2"><h1>Список компаний</h1><div class="companyListHead row"><div class="companyTitleList col-12 col-md-3">global_id</div><div class="companyTitleList col-12 col-md-8">Наименование поддержаной компании</div></div><div class="companyList col-12" v-for="comp in companies"><VisitorCompanyCard :company="comp" :double="true" v-if="comp.duble"></VisitorCompanyCard><VisitorCompanyCard :company="comp" v-else></VisitorCompanyCard></div><div class="companyList col-12 text-center mt-5" v-if="noSearch"><h1>Поиск не дал результатов</h1></div></div></div><div class="contentTabMistake"><div class="companyResult mt-5"></div></div></div><button class="btn" @click="$_vision_index_loadCompany()">Загрузить ещё</button></div>